.icon-left{
    left: 10px;
    position: absolute;
    top: 10px;
}

.icon-right{
    bottom: 10px;
    position: absolute;
    right: 10px;
}

.season-display{
    display: grid;
    justify-content: center;
    align-content: center;
    height: 100vh;
}

h1{
    text-align: center;
}

.winter{ 
    background-color: aliceblue;
}

.summer{
    background-color: orange;
}

.season-display.summer i{
    color: red;
}

.season-display.winter i{
    color: blue;
}